import { Injectable } from '@angular/core';

import * as CryptoJS from 'crypto-js';
import {AlertController} from "ionic-angular";

declare var window: any;

export const environment = {
  production: false,
  //HttpProvider: "http://localhost:7545",
  HttpProvider: "https://rinkeby.infura.io/EU0TpB2ZTZvVWhC7qM7a"
};

@Injectable()
export class Web3Service {

	public web3: any;

  constructor(private alertCtrl: AlertController) {
  	//this.checkAndInstantiateWeb3();
  }

  initWeb3(password: string, privateKeyEncrypted: string, addressWallet: string) {
    const Web3 = require('web3');
    const ProviderEngine = require('web3-provider-engine');
    const CacheSubprovider = require('web3-provider-engine/subproviders/cache.js');
    const FixtureSubprovider = require('web3-provider-engine/subproviders/fixture.js');
    const FilterSubprovider = require('web3-provider-engine/subproviders/filters.js');
    const VmSubprovider = require('web3-provider-engine/subproviders/vm.js');
    const HookedWalletSubprovider = require('web3-provider-engine/subproviders/hooked-wallet.js');
    const NonceSubprovider = require('web3-provider-engine/subproviders/nonce-tracker.js');
    const RpcSubprovider = require('web3-provider-engine/subproviders/rpc.js');
    const Transaction = require('ethereumjs-tx');

    var decrypted = null;
      try {
        decrypted = CryptoJS.AES.decrypt(privateKeyEncrypted, password);
      } catch (e){
        decrypted = null;
      }

    let key = decrypted.toString(CryptoJS.enc.Utf8);

    var engine = new ProviderEngine();

    var privateKey = new Buffer(key, 'hex');
    var address = new Buffer(addressWallet, 'hex');
    var addressHex = '0x'+address.toString('hex');

    // static results
    var providerC = engine.addProvider(new FixtureSubprovider({
      web3_clientVersion: 'ProviderEngine/v0.0.0/javascript',
      net_listening: true,
      eth_hashrate: '0x00',
      eth_mining: false,
      eth_syncing: true
    }));

    // cache layer
    engine.addProvider(new CacheSubprovider());

    // filters
    engine.addProvider(new FilterSubprovider());

    // pending nonce
    var providerB = engine.addProvider(new NonceSubprovider());

    // vm
    engine.addProvider(new VmSubprovider());

    // id mgmt
    var providerA = engine.addProvider(new HookedWalletSubprovider({
      getAccounts: function(cb){
        cb(null, [addressHex])
      },
      signTransaction: function(txParams, cb){
        var tx = new Transaction(txParams);
        tx.sign(privateKey);
        var rawTx = '0x'+tx.serialize().toString('hex');
        cb(null, rawTx);
      }
    }));

    // data source
    engine.addProvider(new RpcSubprovider({
      rpcUrl: environment.HttpProvider,
    }));


    // log new blocks
    // engine.on('block', function(block){
    //   console.log('================================')
    //   console.log('BLOCK CHANGED:', '#'+block.number.toString('hex'), '0x'+block.hash.toString('hex'))
    //   console.log('================================')
    // });

    // network connectivity error
    engine.on('error', function(err){
      // report connectivity errors
      console.error(err.stack)
    });

    // start polling for blocks
    engine.start();

    var web3 = new Web3(engine);
    this.web3 = web3;
  }

  // checkAndInstantiateWeb3 = () => {
  //   // Checking if Web3 has been injected by the browser (Mist/MetaMask)
  //   if (typeof window.web3 !== 'undefined') {
  //     console.warn
  //       'Using web3 detected from external source. If you find that your accounts don\'t appear or you have 0 MetaCoin, ensure you\'ve configured that source properly. If using MetaMask, see the following link. Feel free to delete this warning. :) http://truffleframework.com/tutorials/truffle-and-metamask'
  //     );
  //     // Use Mist/MetaMask's provider
  //     this.web3 = new Web3(window.web3.currentProvider);
  //   } else {
  //     console.warn(
  //       'No web3 detected. Falling back to ${environment.HttpProvider}. You should remove this fallback when you deploy live, as it\'s inherently insecure. Consider switching to Metamask for development. More info here: http://truffleframework.com/tutorials/truffle-and-metamask'
  //     );
  //     // fallback - use your fallback strategy (local node / hosted node + in-dapp id mgmt / fail)
  //     this.web3 = new Web3(
  //       new Web3.providers.HttpProvider(environment.HttpProvider)
  //     );
  //   }
  // };
  //
  // getAccounts(): Observable<any>{
  // 	return Observable.create(observer => {
  // 	  this.web3.eth.getAccounts((err, accs) => {
  // 	    if (err != null) {
  // 	      observer.error('There was an error fetching your accounts.')
  // 	    }
  //
  // 	    if (accs.length === 0) {
  // 	      observer.error('Couldn\'t get any accounts! Make sure your Ethereum client is configured correctly.')
  // 	    }
  //
  // 	    observer.next(accs)
  // 	    observer.complete()
  // 	  });
  // 	})
  // }

}



